.loader-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500px;
}

.loader {
  width: 48px;
  height: 48px;
  border: 3px solid #fff;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  margin: 20px 0;
}
.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-bottom-color: #ff6060;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.carousel {
  width: 100%;
  height: 415px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 25px;
  overflow: hidden;

  @media (max-width: 1024px) {
    height: 255px;
  }

  &__image {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__arrow {
    position: absolute;
    cursor: pointer;

    @media (max-width: 1024px) {
      width: 24px;
      height: 24px;
    }

    &__left {
      left: 0px;
    }
    &__right {
      right: 0px;
    }
  }
  &__picture-counter {
    position: absolute;
    bottom: 0;
    color: white;
    font-weight: 500;
    font-size: 18px;

    @media (max-width: 1024px) {
      display: none;
    }
  }
}
