.tags-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 115px;
    height: 25px;
    border-radius: 10px;
    background-color: #ff6060;
    color: white;
    font-weight: 700;
    font-size: 14px;
    margin: 0 10px 0 0;
    padding: 0 5px;

    @media (max-width: 1024px) {
      height: 18px;
      font-size: 10px;
      min-width: 84px;
      margin: 0 10px 5px 0;
    }
  }
}
