.logements-list {
  padding: 55px 50px 45px 50px;
  background-color: #f6f6f6;
  border-radius: 25px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 60px;
  grid-row-gap: 50px;
  margin-bottom: 50px;

  @media (max-width: 1024px) {
    background-color: inherit;
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
    padding: inherit;
    margin-bottom: 0px;
  }
}
